@import "../../styles/variables.scss";

.facts {
    &__title {
        font-size: 1.5rem;
        line-height: 1.5;
    }
    &__list {
        max-height: auto;
        &--short {
            max-height: 312px;
            overflow-y: hidden;
        }
    }
    &__list-item {
        position: relative;
        padding: 16px 0 16px 24px;
        border-bottom: 1px solid $grey;
        list-style-position: inside;
        list-style-type: none;
        font-size: 0.875rem;
        &::before {
            position: absolute;
            top: 20px;
            left: 0;
            content: "";
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: $white;
        }
        a {
            color: $primary;
            padding-bottom: 2px;
            border-bottom: 1px dashed $primary;
        }
    }
    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        margin: 10px 0;
        padding: 7px 16px 9px 11px;
        background-color: $primary;
        color: $white;
        cursor: pointer;
        &::before {
            content: "";
            display: block;
            width: 16px;
            height: 16px;
            margin-right: 8px;
            background-image: url("../../assets/images/small-arrow-down.svg");
            background-repeat: no-repeat;
            background-position: center center;
        }
        &--rotated {
            &::before {
                transform: rotate(180deg);
            }
        }
        &:hover {
            opacity: 0.8;
        }
    }
}
